/* Typography
----------------------------------------- */
*,
*:after,
*:before {
  box-sizing: border-box; }

html,
button,
input,
select,
textarea {
  color: #333; }

::-moz-selection {
  background: #358AE4;
  color: white;
  text-shadow: none; }

::selection {
  background: #358AE4;
  color: white;
  text-shadow: none; }

hr {
  border: none;
  clear: both;
  margin: 44px auto 54px;
  width: 65%;
  border-bottom: 5px solid #f5f5f5;
  display: inline-block;
  left: 50%;
  margin-left: -32.5%;
  position: relative; }

abbr, acronym {
  text-transform: uppercase;
  font-size: 90%;
  color: #222;
  border-bottom: 1px solid #cccccc;
  cursor: help; }

abbr {
  text-transform: none; }

img {
  vertical-align: middle;
  interpolation-mode: bicubic; }

/*
* Flexible Media
*/
img, video, canvas, object, embed {
  max-width: 100%;
  height: auto; }

/*
* Flexible Video iFrame Embed
* use like :
<figure class="aspect">
	<iframe src="https://www.youtube.com/embed/4Fqg43ozz7A"></iframe>
</figure>
*/
figure.aspect {
  height: 0;
  padding-bottom: 56.25%;
  /* 16:9 */
  position: relative;
  overflow: hidden; }
  figure.aspect iframe,
  figure.aspect video,
  figure.aspect embed {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%; }

fieldset {
  border: 0;
  margin: 0;
  padding: 0; }

textarea {
  resize: vertical; }

.chromeframe {
  margin: 0.2em 0;
  background: #ccc;
  color: #000;
  padding: 0.2em 0; }

pre {
  font-family: Monaco;
  padding: 0.5em 1em;
  margin: 0 0 1rem;
  border-radius: 2px;
  overflow-x: scroll; }
  pre code {
    overflow-x: scroll;
    border: none;
    word-wrap: initial;
    background-color: transparent; }

code {
  font-size: 12px;
  font-size: 0.75em;
  font-size: 0.75rem;
  line-height: 1.5;
  margin: 0 2px;
  padding: 0 5px;
  border: 1px solid #eaeaea;
  background-color: #f7f7f7; }

html,
body {
  font-size: 100%; }

html {
  font-family: sans-serif;
  text-size-adjust: 100%;
  text-rendering: optimizeLegibility; }

body {
  line-height: 1;
  font-family: "Open Sans", helvetica, sans-serif;
  color: #333;
  -webkit-font-smooth: always;
  -webkit-font-smoothing: antialiased;
  text-shadow: 0 1px 0 rgba(0, 0, 0, 0.01);
  background: #fff;
  overflow-scrolling: touch;
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
  text-size-adjust: none; }

[class*='text-align-'] {
  text-align: center; }

@media (min-width: 960px) {
  .text-align-left {
    text-align: left !important; }
  .text-align-right {
    text-align: right !important; }
  .text-align-center {
    text-align: center !important; } }

